import "./SecaoComentario.css";
import React, { useState } from "react";

const SecaoComentarios = ({ LivroId }) => {
    // Correo na desestruturao do useState
    const [comentario, setComentario] = useState('');

    const handleInputChange = (event) => {
        setComentario(event.target.value);
    };

    const handleSubmit = async (event) => {
        event.preventDefault();
        console.log('Enviando comentrio:', { LivroId, comentario });
        
        try {
            const response = await fetch('/api/comentarios', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    LivroId,
                    comentario
                }),
            });
    
            if (response.ok) {
                console.log('Comentrio enviado com sucesso!');
                setComentario('');
            } else {
                console.error('Erro ao enviar o comentrio', response.status);
            }
    
        } catch (error) {
            console.error('Erro ao enviar o comentrio', error);
        }
    };
    

    return (
        <section className="container-comentario">
            <form onSubmit={handleSubmit}>
                <textarea
                    className="comentarios"
                    value={comentario}
                    onChange={handleInputChange}
                    placeholder="Digite seu comentrio..."
                    required
                />
                <button className="btn-submit" type="submit">Comentar</button>
            </form>
        </section>
    );
};

export default SecaoComentarios;
